export default {
    methods: {
        clickGood() {
            this.$emit('click', {
                type: 'good',
                detail: {
                    ...this.info
                }
            })
        }
    },
    props: {
        info: {
            type: Object,
            default () {
                return {
                    img: '',
                    goodName: '',
                    color: '',
                    size: '',
                    num: 0,
                }
            }
        }
    },
}
